.toy-details {
    width: 500px;
    height: 500px;
    margin: auto;
    margin-top: 100px;
    background-color: $clr1;

    >* {
        text-align: center;
        text-decoration: none;
    }

    .back-btn {
        float: right;
        cursor: pointer;
        width: 55px;
        height: 30px;
        border: rgb(255, 255, 255);
        background-color: rgb(200, 189, 189);

        &:hover {
            background-color: $clr1;
        }
    }

    section img {
        margin: auto;
    }

}