// Flex 
.flex {
    display: flex;
}
.inline-flex {
    display: inline-flex;
}

// List 
.clean-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

// Text 
.vertical-text {
    writing-mode: tb-rl;
    transform: rotate(-180deg);  
}

.tag-text {
    color: $clr1;
    text-transform: uppercase;
}